import {
  Meta,
  Links,
  Outlet,
  Scripts,
  LiveReload } from
'@remix-run/react';
import styles from '~/styles/index.css';
import Header from '~/components/header';
import Footer from '~/components/footer';

export function meta() {
  return (
    [
    { title: 'GuitarLA - Remix' },
    { property: 'charset', content: 'utf-8' },
    { property: 'viewport', content: 'witdh=device-width,initial-scale=1' }]);


}

export function links() {
  return [
  {
    rel: 'stylesheet',
    href: 'https://necolas.github.io/normalize.css/8.0.1/normalize.css'
  },
  {
    rel: 'stylesheet',
    href: styles
  },
  {
    rel: 'preconnect',
    href: 'https://fonts.googleapis.com'
  },
  {
    rel: 'preconnect',
    href: 'https://fonts.gstatic.com',
    crossOrigin: 'true'
  },
  {
    rel: 'stylesheet',
    href: 'https://fonts.googleapis.com/css2?family=Lato:wght@700;900&family=Outfit:wght@400;700;900&display=swap'
  }];

}

export default function App() {
  return (
    <Document>
            <Outlet />
        </Document>);

}

function Document(params: any) {
  return (
    <html lang="es">
            <head>
                <Meta />
                <Links />
            </head>
            <body>
                <Header />
                {params.children}
                <Footer />
                <Scripts />
                <LiveReload />
            </body>
        </html>);

}